import type { Client } from "@/types/admin";
import { mockClients } from "@/mocks/clients";

/**
 * Data adapter cho Clients
 */

let clientsData: Client[] = [...mockClients];

export async function listClients(): Promise<Client[]> {
  await new Promise((resolve) => setTimeout(resolve, 100));
  return [...clientsData];
}

export async function getClient(id: string): Promise<Client | null> {
  await new Promise((resolve) => setTimeout(resolve, 50));
  return clientsData.find((client) => client.id === id) || null;
}

export async function convertLeadToClient(leadId: string): Promise<Client> {
  await new Promise((resolve) => setTimeout(resolve, 200));
  // In Phase 3, this would fetch lead from DB and create client
  // For now, create a mock client
  const newClient: Client = {
    id: `client-${Date.now()}`,
    companyName: `Company from Lead ${leadId}`,
    contactName: "Contact Name",
    email: "contact@example.com",
    status: "onboarding",
    linkedLeads: [leadId],
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
  };
  clientsData.push(newClient);
  return newClient;
}

