import { AdminPageShell, AdminSectionCard } from "@prosfin/ui";
import { getClient } from "@/lib/data/clients";

export default async function ClientDetailPage({
  params,
}: {
  params: { id: string };
}) {
  const client = await getClient(params.id);

  if (!client) {
    return (
      <AdminPageShell title="Client Not Found">
        <p>Client not found</p>
      </AdminPageShell>
    );
  }

  return (
    <AdminPageShell title={client.companyName} description="Client details">
      <div className="grid gap-6 md:grid-cols-2">
        <AdminSectionCard title="Company Profile">
          <div className="space-y-4">
            <div>
              <p className="text-muted-foreground text-sm">Company Name</p>
              <p className="font-medium">{client.companyName}</p>
            </div>
            <div>
              <p className="text-muted-foreground text-sm">Contact Name</p>
              <p className="font-medium">{client.contactName}</p>
            </div>
            <div>
              <p className="text-muted-foreground text-sm">Email</p>
              <p className="font-medium">{client.email}</p>
            </div>
            {client.phone && (
              <div>
                <p className="text-muted-foreground text-sm">Phone</p>
                <p className="font-medium">{client.phone}</p>
              </div>
            )}
          </div>
        </AdminSectionCard>
        <AdminSectionCard title="Linked Leads">
          <p className="text-muted-foreground text-sm">
            {client.linkedLeads?.length || 0} linked lead(s)
          </p>
        </AdminSectionCard>
      </div>
    </AdminPageShell>
  );
}

